.block-letter {
  display: flex;
  justify-content: space-between;
  .col-padding();
  .lg(flex-direction, column);
  .lg(text-align, center);
  &-item {
    flex-basis: 49%;
    .lg(flex-basis, 100%);
  }
  &__title {
    margin-bottom: 16px;
    font-size: 48px;
    .lg(font-size, 30px);
    .xs(font-size, 24px);
    font-weight: 700;
    letter-spacing: 1.75px;
    line-height: 38px;
    color: #44444f;
  }
  &__text {
    font-family: "Open Sans", serif;
    font-size: 15px;
    line-height: 24px;
    letter-spacing: 0.75px;
    .lg(max-width, 500px);
    .lg(margin, 9px auto 30px);
  }
  &__input {
    width: 100%;
    display: block;
    box-sizing: border-box;
    padding: 15px 0 15px 30px;
    .lg(padding, 15px 0);
    background-color: @bg-dark-color;
    font-family: "Open Sans", serif;
    letter-spacing: 0.75px;
    color: @gray-color;
    border: none;
    border-radius: 3px;
    .lg(max-width, 500px);
    .lg(margin, 9px auto 0);
    .lg(text-align, center);
    &:focus {
      outline: none;
    }
    &::placeholder {
      color: @gray-color;
    }
  }
}
