.footer {
  background-color: @bg-dark-color;
  color: @gray-color;
  padding: 160px 0 157px;
  .lg(padding, 100px 0);
}
.block-footer {
  display: flex;
  justify-content: space-between;
  flex-basis: 100%;
  .col-padding();
  .lg(flex-direction, column);
  .lg(text-align, center);
  .lg(margin, 0 auto);
  &__logo {
    &:focus {
      outline: none;
    }
  }
  &-item {
    .lg(margin, 0 auto);
    &:first-child {
      flex-basis: 43%;
      .lg(flex-basis, 300px);
    }
    &:nth-child(2) {
      flex-basis: 34%;
      .lg(flex-basis, 270px);
    }
    &:nth-child(3) {
      flex-basis: 23%;
      .lg(flex-basis, 150px);
    }
  }
  &__title {
    color: @accent-color;
    font-size: 18px;
    letter-spacing: 0.9px;
    margin-top: 11px;
    text-transform: uppercase;
  }
  &__desc {
    margin-top: 24px;
    max-width: 370px;
    line-height: 24px;
    font-family: "Open Sans", sans-serif;
    letter-spacing: 0.6px;
  }
  &__copyright {
    font-family: "Open Sans", sans-serif;
    letter-spacing: 0.6px;
    margin-top: 28px;
  }
  &__list {
    font-family: "Open Sans", sans-serif;
    margin: 20px 0 0;
    padding-left: 0;
    list-style: none;
    & > li {
      line-height: 28px;
      letter-spacing: 0.6px;
      & > a {
        text-decoration: none;
        color: @gray-color;
        &:hover,
        &:focus {
          outline: none;
          color: @accent-color;
        }
      }
    }
    &_social {
      margin-top: 22px;
      & > li {
        line-height: 24px;
      }
    }
  }
  &__social {
    display: flex;
    list-style: none;
    padding-left: 0;
    .lg(justify-content, center);
    & > li {
      display: inline-block;
      margin-right: 12px;
      letter-spacing: 1.62px;
      vertical-align: middle;
      & > a {
        color: @accent-color;
        &:hover,
        &:focus {
          outline: none;
          color: darken(#77e570, 15%);
        }
      }
    }
  }
}
