.block-products {
  display: flex;
  justify-content: space-around;
  flex-flow: row wrap;
  margin-top: 75px;
  .lg(flex-direction, column);
  .lg(width, 350px);
  .lg(margin, 87px auto 0);
  .col-padding();
  &-btn {
    display: inline-block;
    margin: 21px auto 0;
    padding-bottom: 5px;
    border-bottom: 3px solid transparent;
    transition: 0.5s;
    .lg(margin-bottom, 80px);
  }
  a {
    color: #65e496;
    font-family: "Open Sans", sans-serif;
    font-weight: 700;
    letter-spacing: 0.8px;
    text-decoration: none;
    &:hover,
    &:focus {
      border-bottom: 3px solid currentColor;
      outline: none;
    }
  }
  &-item {
    text-align: center;
    flex-basis: 320px;
    &_incept {
      padding-left: 10px;
      padding-right: 20px;
      .lg(padding, 0);
    }
    &_club {
      padding-left: 28px;
      padding-right: 32px;
      .lg(padding, 0);
    }
    &_devil {
      padding-left: 18px;
      padding-right: 12px;
      .lg(padding, 0);
    }
  }
  &-demo {
    display: block;
    background-image: url("../img/svg/sprite.stack.svg#demoBook");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    margin: 0 auto;
    max-width: 269px;
    height: 409px;
    position: relative;
    &__title {
      position: absolute;
      color: @light-color;
      font-size: 24px;
      font-weight: 500;
      letter-spacing: 0.75px;
      right: 38px;
      top: 70px;
      span {
        display: block;
        font-family: "Open Sans", sans-serif;
        font-size: 12px;
        font-weight: 300;
        line-height: 32px;
        color: @accent-color;
      }
    }
    &__author {
      position: absolute;
      font-family: "Open Sans", sans-serif;
      font-size: 12px;
      font-weight: 300;
      letter-spacing: 0.96px;
      bottom: 32px;
      left: 80px;
      color: #65e496;
    }
  }
  &-text {
    & > h3 {
      color: #44444f;
      font-weight: 500;
      margin: 34px auto 0;
    }
    & > p {
      font-family: "Open Sans", sans-serif;
      letter-spacing: 0.75px;
      line-height: 24px;
      margin-top: 15px;
    }
  }
}
