.main-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 38px;
  padding-bottom: 166px;
  .col-padding();
  .lg(flex-direction, column);
  .lg(align-items, center);
  .lg(padding-bottom, 100px);
  .md(align-items, center);
  .md(flex-direction, column);
  &__logo {
    .md(margin-bottom, 10px);
    &:focus {
      outline: none;
    }
  }
  &__toggle {
    width: 50px;
    height: 50px;
    background-color: transparent;
    position: absolute;
    left: -65px;
    top: 10px;
    border-radius: 100%;
  }
  &__bars {
    cursor: pointer;
    border-radius: 1px;
    height: 2px;
    width: 25px;
    background: #fff;
    position: absolute;
    left: 12px;
    top: 50%;
    display: block;
    transition: all 0.3s;
    &::before,
    &::after {
      cursor: pointer;
      border-radius: 1px;
      height: 2px;
      width: 25px;
      background: #fff;
      position: absolute;
      left: 12px;
      top: 50%;
      display: block;
      transition: all 0.3s;
      content: "";
    }
    &::before {
      left: 0;
      top: -8px;
    }
    &::after {
      left: 0;
      top: 8px;
    }
  }
  &__menu {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    &_offcanvas {
      position: fixed;
      right: 0;
      top: 0;
      z-index: 99;
      width: 15%;
      min-width: 200px;
      height: 100vh;
      background-color: darken(@bg-dark-color, 3%);
      transform: translateX(100%);
      & .main-header__toggle {
        background-color: darken(@bg-dark-color, 3%);
      }
      & .header-list__item {
        opacity: 0;
        transition: opacity 0.5s 0.5s;
      }
    }
    &_active {
      transform: translateX(0%);
      transition: 0.5s;
      .header-list__item {
        opacity: 1;
      }
      .mixin(@n, @menu-delay) when (@n > 0) {
        .header-list__item:nth-child(@{n}) {
          transition-delay: @menu-delay;
        }
        .mixin(@n - 1, @menu-delay - 0.25s);
      }
      .mixin(8, 2.25s);

      .main-header__bars {
        background-color: transparent;
        &::before,
        &::after {
          top: 0;
        }
        &::before {
          transform: rotate(45deg);
        }
        &::after {
          transform: rotate(-45deg);
        }
      }
    }
  }
}

.header {
  &-list {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    list-style: none;
    font-family: "Open Sans", serif;
    font-weight: 300;
    white-space: nowrap;
    padding: 0;
    margin-left: -21px;
    .md(flex-wrap, wrap);
    .md(justify-content, center);
    .md(max-width, 460px);
    .xs(max-width, 285px);
    &_offcanvas {
      height: 65vh;
      padding-right: 70px;
      flex-direction: column;
      text-transform: uppercase;
      .md(margin-bottom, 0);
      .md(justify-content, space-between);
      .sm(height, 95vh);
      .xs(height, 75vh);
      .xs(justify-content, space-between);
    }
    &__item {
      margin-left: 21px;
      padding-bottom: 5px;
      border-bottom: 3px solid transparent;
      transition: 0.3s;
      font-size: 17px;
      .md(margin-bottom, 10px);
    }
    &__link {
      text-decoration: none;
      color: @light-color;
      letter-spacing: 0.5px;
      &:hover,
      &:focus {
        outline: none;
        border-bottom: 3px solid @accent-color;
      }
    }
  }
}
