.block-blog {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  margin-top: 75px;
  .md(flex-flow, column nowrap);
  .md(text-align, center);
  .md(margin, 86px auto 0);
  .md(max-width, 550px);
  .xs(margin-top, 60px);
  .col-padding();
  h4 {
    margin-top: 18px;
    font-size: 18px;
    letter-spacing: 0.9px;
    color: #44444f;
  }
  &-img {
    max-width: 369px;
    height: 224px;
    overflow: hidden;
    position: relative;
    .md(max-width, 100%);
    .md(height, 400px);
    .sm(height, 300px);
    .xs(height, 200px);
    img {
      position: absolute;
      width: 100%;
      height: 224px;
      .md(height, 400px);
      .sm(height, 300px);
      .sm(width, 80%);
      .xs(height, 200px);
      .xs(width, 90%);
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      object-fit: cover;
    }
  }
  &-item:nth-child(2) &-img {
    .md(max-width, 100%);
    .sm(height, 300px);
    .xs(height, 200px);
    max-width: 372px;
    img {
      width: 100%;
      .md(height, 400px);
      .sm(height, 300px);
      .sm(width, 80%);
      .xs(height, 200px);
      .xs(width, 90%);
    }
  }
  &-info {
    margin-top: 10px;
    font-size: 15px;
    letter-spacing: 0.75px;
    font-family: "Open Sans", serif;
    a {
      color: @accent-color;
      text-decoration: none;
      &:hover,
      &:focus {
        outline: none;
        color: darken(#77e570, 15%);
      }
    }
  }
  &-item {
    flex-basis: 32%;
    .md(margin, 0 auto 80px);
    &__desc {
      margin-top: 15px;
      line-height: 24px;
      font-size: 15px;
      letter-spacing: 0.75px;
      font-family: "Open Sans", serif;
    }
  }
}
