.block-book {
  display: flex;
  justify-content: space-between;
  padding: 0;
  flex-basis: 500px;
  .col-padding();
  .lg(text-align, center);
  .lg(align-items, center);
  .lg(flex-direction, column-reverse);
  &-demo {
    background-image: url("../img/svg/sprite.stack.svg#theBook");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    .lg(flex-basis, 640px);
    .lg(margin, 70px auto 0);
    .xs(flex-basis, 359px);
    .xs(width, 250px);
    width: 445px;
    height: 639px;
    position: relative;
    &__title {
      .xs(font-size, 22px);
      .xs(left, 52px);
      .xs(top, 83px);
      font-size: 36px;
      letter-spacing: 1.96px;
      font-weight: 500;
      top: 108px;
      left: 128px;
      position: absolute;
      span {
        font-family: "Open Sans", serif;
        font-size: 14px;
        .sm(font-size, 12px);
        line-height: 24px;
        letter-spacing: 0.7px;
        font-weight: 400;
        display: block;
        color: @accent-color;
      }
    }
    &__author {
      bottom: 60px;
      left: 85px;
      color: #65e496;
      font-family: "Open Sans", sans-serif;
      font-size: 14px;
      .sm(font-size, 12px);
      .sm(left, 65px);
      .sm(bottom, 35px);
      .xs(left, 50px);
      .xs(bottom, 20px);
      letter-spacing: 0.82px;
      position: absolute;
    }
  }
}

.block-text {
  flex-basis: calc(100% - 500px);
  .lg(flex-basis, calc(92% - 315px));
  &_events {
    flex-basis: calc(100% - 600px);
  }
  &__name {
    font-family: "Open Sans", serif;
    font-size: 18px;
    letter-spacing: 0.75px;
    margin-bottom: 30px;
    &_author {
      margin-top: 16px;
      margin-bottom: 20px;
      & > span {
        .sm(font-size, 15px);
      }
    }
    &_events {
      margin-top: 0;
      margin-bottom: 20px;
    }
  }
  &__title {
    font-size: 48px;
    .lg(font-size, 30px);
    .xs(font-size, 24px);
    font-weight: 500;
    letter-spacing: 1.92px;
    margin-bottom: 40px;
    &_author {
      letter-spacing: 1px;
      color: #44444f;
    }
    &_events {
      margin-bottom: 44px;
      letter-spacing: 1.92px;
      color: #44444f;
    }
    span {
      display: block;
      color: @accent-color;
      font-size: 18px;
      .xs(font-size, 15px);
      margin-top: 9px;
      letter-spacing: 0.9px;
    }
  }
  &__desc {
    font-family: "Open Sans", serif;
    max-width: 570px;
    .lg(max-width, initial);
    font-size: 15px;
    line-height: 24px;
    margin-bottom: 24px;
    letter-spacing: 0.7px;
  }
  small {
    font-family: "Open Sans", serif;
    font-style: italic;
    font-size: 12px;
    line-height: 22px;
    letter-spacing: 0.6px;
  }
}
