.block-author {
  display: flex;
  justify-content: space-between;
  .col-padding();
  .sm(flex-direction, column-reverse);
  .sm(text-align, center);
  &_events {
    flex-basis: 600px;
    .sm(flex-direction, column);
    padding-bottom: 125px;
    .lg(padding-bottom, 0);
  }
}
.block-ava {
  background-image: url("../img/svg/sprite.stack.svg#bgAva");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  width: 435px;
  height: 558px;
  position: relative;
  .lg(width, 300px);
  .lg(height, 412px);
  .sm(margin, 0 auto 50px);
  .xs(height, 260px);
  .xs(width, 200px);
  &_events {
    width: 435px;
    height: 704px;
    background-image: url("../img/svg/sprite.stack.svg#bgEmma");
    .lg(width, 300px);
    .lg(height, 510px);
    .xs(width, 200px);
    .xs(height, 325px);
  }
  &__img {
    position: absolute;
    top: 48px;
    right: 44px;
    .lg(width, 300px);
    .xs(top, 25px);
    .xs(right, 20px);
    .xs(width, 200px);
    &_events {
      top: 48px;
      left: 51px;
      .xs(top, 25px);
      .xs(left, 20px);
    }
  }
}

.block-counter {
  display: flex;
  flex-wrap: wrap;
  margin-top: 140px;
  .col-padding();
  .lg(margin-top, 110px);
  .sm(flex-direction, column);
  & dt {
    color: @accent-color;
    font-weight: 100;
    font-size: 80px;
    .xs(font-size, 60px);
    text-align: center;
    letter-spacing: 3.2px;
  }
  & dd {
    margin: 0;
    font-family: "Open Sans", serif;
    font-weight: 300;
    text-align: center;
    padding: 12px 0;
    font-size: 18px;
    .sm(font-size, 15px);
    letter-spacing: 1.2px;
  }
  &__item {
    flex-basis: 36%;
    flex-grow: 1;
    .sm(margin-bottom, 80px);
    &:nth-child(2n + 1) {
      flex-basis: 28%;
      flex-grow: 2;
    }
  }
}
