.block-slider {
  text-align: center;
  .col-padding();
  h3 {
    color: @accent-color;
    letter-spacing: 1.2px;
    line-height: 30px;
  }
  &__expert {
    color: @gray-color;
    font-family: "Open Sans", sans-serif;
    line-height: 26px;
    letter-spacing: 0.75px;
    margin-bottom: 32px;
    font-weight: 300;
  }
  &__desc {
    color: @gray-color;
    font-family: "Open Sans", sans-serif;
    letter-spacing: 0.75px;
    max-width: 760px;
    margin: 0 auto;
    line-height: 28px;
  }
  &-ava {
    position: relative;
    display: block;
    margin: 0 auto 25px;
    width: 152px;
    height: 152px;
  }
  &-thumb {
    position: relative;
    display: block;
    margin: 0 auto;
    overflow: hidden;
    width: inherit;
    height: inherit;
    border-radius: 100%;
    img {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      object-fit: cover;
    }
  }
  &-bq {
    background-image: url("../img/svg/sprite.stack.svg#quote");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    display: block;
    width: 48px;
    height: 42px;
    position: absolute;
    top: 0;
    right: -14px;
  }
}

.slick-dots {
  display: flex;
  justify-content: center;
  list-style: none;
  padding: 25px 0 0;
  & > li {
    margin: 0 5px;
    & > button {
      text-indent: -9999px;
      width: 8px;
      height: 8px;
      border: none;
      background-color: @accent-color;
      border-radius: 100%;
      line-height: 0;
      font-size: 0;
      cursor: pointer;
      padding: 5px;
      &:focus {
        outline: none;
      }
    }
  }
  & > li.slick-active {
    & > button {
      width: 37px;
      border-radius: 8px;
    }
  }
}
