.block-about {
  display: flex;
  justify-content: space-between;
  margin-top: 75px;
  .col-padding();
  .lg(flex-direction, column);
  .lg(margin, 75px auto 0);
  .lg(max-width, 750px);
  &-text {
    flex-basis: calc(90% - 574px);
    .lg(flex-basis, 100%);
    .lg(text-align, center);
  }
  &-text__desc {
    font-family: "Open Sans", serif;
    letter-spacing: 0.75px;
    .lg(letter-spacing, 0.86px);
    line-height: 24px;
    margin-top: 18px;
    font-size: 15px;
  }
  &-social {
    align-items: center;
    .lg(justify-content, center);
    display: flex;
    margin-top: 31px;
    &__title {
      color: #44444f;
      font-size: 18px;
      font-weight: 500;
      letter-spacing: 0.9px;
      margin-left: 3px;
    }
    &__list {
      list-style: none;
      flex-direction: row;
      margin: 0;
      padding: 4px 0 0 20px;
      & > li {
        display: inline-block;
        margin-right: 8px;
        & > a {
          color: @accent-color; 
          &:hover,
          &:focus {            
            outline: none;
            color: darken(#77E570, 15%);
          }
        }
      }
    }
  }
  &-table {
    flex-basis: 574px;
    .lg(flex-basis, 100%);
    .lg(margin-top, 50px);
    & > h3 {
      .lg(text-align, center);
      .xs(font-size, 20px);
    }
    &__list {
      column-count: 2;
      column-gap: 30px;
      counter-reset: list;
      margin: 24px 0 0 2px;
      padding: 0;
      .xs(column-count, 1);
      .xs(margin, 24px auto 0);
      .xs(width, 279px);
      & > li {
        counter-increment: list;
        font-family: "Open Sans", serif;
        letter-spacing: 0.75px;
        list-style: none;
        margin-bottom: 12px;
        padding-left: 34px;
        padding-top: 3px;
        position: relative;
        &::before {
          content: counter(list) ". ";
        }
        &::after {
          background-color: #5fc9b7;
          color: #fff;
          content: "+";
          font-family: "Open Sans", serif;
          left: 0;
          top: 0;
          padding: 0 6px;
          line-height: 22px;
          position: absolute;
        }
      }
      &_next {
        counter-reset: list 10;
        position: relative;
        &::before {
          content: "";
          background-color: #5fc9b7;
          display: block;
          height: 3px;
          position: absolute;
          top: -19px;
          width: 100px;
        }
      }
    }
  } /* block-about-table */
  &-toggle {
    margin-top: 27px;
    position: relative;
    .lg(margin, 27px auto 0);
    .lg(width, 135px);
  }
}
