h1,
h2,
h3,
h4,
p {
  padding: 0;
  margin: 0;
}

h3 {
  font-size: 24px;
  letter-spacing: 1.4px;
  color: #44444f;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  font-family: "Roboto Slab", serif;
  font-size: 15px;
}

.container {
  .wrapper();
}

.row {
  .row-flex();
}

.page-main {
  background-image: url("../img/svg/sprite.stack.svg#bg-main");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  color: @light-color;
  padding-bottom: 160px;
}

.page {
  padding-top: 160px;
  padding-bottom: 150px;
  color: #696980;
  .lg(padding, 120px 0);
  .xs(padding, 70px 0);
  position: relative;
  z-index: 1;
  &__title {
    font-size: 48px;
    .lg(font-size, 30px);
    .lg(margin-bottom, 12px);
    .xs(font-size, 24px);
    color: #44444f;
    letter-spacing: 1.92px;
    text-align: center;
  }
  &__subtitle {
    color: @accent-color;
    font-size: 18px;
    .xs(font-size, 15px);
    line-height: 44px;
    .lg(line-height, 18px);
    letter-spacing: 0.9px;
    text-align: center;
  }
  &__triangle {
    position: absolute;
    z-index: 1;
    bottom: 0; 
    width: 100%;
    height: 64px;
    .xs(height, 20px);
    .lg(height, 40px);
    fill: currentColor;
  }
  &_author {
    padding-bottom: 135px;
    .lg(padding-bottom, 80px);
  }
  &_gray {
    background-color: @bg-light-color;
    padding-top: 145px;
    padding-bottom: 255px;
  }
  &_books {
    padding-top: 145px;
    padding-bottom: 220px;
    position: relative;
    background-repeat: no-repeat;
  }
  &_slider {
    background-color: #44444f;
    padding-bottom: 205px;
    position: relative;
    background-repeat: no-repeat;
  }
  &_events {
    padding-top: 160px;
    padding-bottom: 10px;
    .lg(padding-bottom, 80px);
  }
  &_letter {
    padding-bottom: 155px;
  }
  &_blog {
    padding-top: 148px;
    padding-bottom: 162px;
  }
}

.btn {
  display: block;
  padding: 13px 0 16px;
  background-image: linear-gradient(to left, #65e49c, #65e4b0);
  box-shadow: 0 -2px #59ba88 inset;
  color: @light-color;
  border-radius: 3px;
  font-size: 18px;
  .xs(font-size, 15px);
  font-weight: 400;
  letter-spacing: 0.75px;
  text-align: center;
  text-decoration: none;
  position: relative;
  &:before,
  &:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
  }
  &:before {
    height: 1px;
    background: rgba(white, 0.4);
  }
  &:after {
    height: 100%;
    border-radius: 3px;
    box-shadow: 0 2px 5px 0 rgba(black, 0.3);
  }
  &:hover,
  &:focus {
    outline: none;
    text-decoration: none;
    background-image: linear-gradient(to top left, #65e47d, #65e46b);
  }
  &:active {
    top: 1px;
    &:after {
      box-shadow: 0 1px 5px 0 rgba(#65e49c, 0.3);
    }
  }
  &_block-book {
    width: 250px;
    margin: 48px 0 19px;
    .lg(margin, 47px auto 19px);
    .xs(max-width, 220px);
  }
  &_book-author {
    width: 238px;
    margin: 48px 0 19px;
    .sm(margin, 47px auto 19px);
    .xs(max-width, 220px);
    border: none;
    cursor: pointer;
  }
  &_block-about {
    width: 135px;
    margin-left: 4px;
    .lg(margin-left, 0);
    border: none;
    cursor: pointer;
  }
  &_up {
    position: absolute;
    top: 0;
  }
  &_block-products {
    width: 220px;
    margin: 80px auto 0;
    .lg(margin-top, 40px);
  }
  &_block-events {
    width: 136px;
    margin: 48px 0 19px;
    .sm(margin-left, auto);
    .sm(margin-right, auto);
  }
  &_block-blog {
    width: 265px;
    margin: 80px auto 0;
    .md(margin-top, 0);
  }
  &_block-letter {
    width: 100%;
    margin-top: 21px;
    border: none;
    .lg(max-width, 500px);
    .lg(margin, 21px auto 0);
  }
}
